//Menu and Button on nav should disappear under 768px of screen
@mixin hideMenu {
  @media screen and (max-width: 768px) {
    display: none;
  }
}

nav {
  height: 60px;
  display: flex;
  justify-content: space-between;
  padding: 1rem 2rem;
  z-index: 100;
  position: fixed;
  width: 100%;
  align-items: center;
}

@mixin navlinks($transform: false) {
  color: #fff;
  align-items: center;
  padding: 0 1rem;
  cursor: pointer;
  text-decoration: none;
  text-shadow: 3px 0px 1px black;

  &:hover {
    @if $transform {
      transform: scale(1.2);
    }
  }
}

.nav__logo {
  @include navlinks($transform: false);
  font-style: italic;
  text-shadow: 2px 2px 1px rgb(9, 2, 2);
}

.nav__burgerMenu {
  @media screen and (min-width: 768px) {
    display: none;
  }
  color: white;
  display: block;
  background-size: contain;
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(-80%, 20%);
  height: 40px;
  width: 40px;
}

.nav__menu {
  display: flex;
  align-items: center;
  margin-right: -60px;

  @include hideMenu();
}

.nav__button {
  display: flex;
  align-items: center;
  margin-right: 24px;
  @include hideMenu();

  a {
    color: #fff;
  }
}
